/*   ----- Variables  -----  */
:root {
  --groundBrown: #4D3F26;
  --brightGreen: #DDFF61;
  --mellowYellow: #9FB551;
  --darkGreen: #2B4701;
  --forestGreen: #758C42;
}

/*  ----- Viewport ----- elements inherited by all remaining elements ----- */
body {       /* Can alternatively define in html {} or :root {} */
  background-color: var(--mellowYellow);
  background-image: url(images/background-pfizerkoipond.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: top left;
  background-repeat: no-repeat;
  color:var(--groundBrown);
  font-family: 'Playfair Display', serif; /* Define with at least one font name and its related generic family name.*/
  margin: 2%;
  padding: 2%;
  max-width: 100;
}

/*  ----- Page layout blocks -----  */
header {background-color: var(--darkGreen);
  color: var(--brightGreen);
  margin:2%;
  padding:2%;
  text-align: center;}
main {background-color: var(--forestGreen);
  color:var(--brightGreen);
  margin:2%;
  padding:2%;}
section {background-color: var(--groundBrown);
  color:var(--brightGreen);
  margin:2%;
  padding:2%;}
  article {background-color: var(--mellowYellow);
    color:var(--groundBrown);
    margin:2%;
    padding:2%;}
    #topic1 {}
    #topic2 {}
div {}
aside {}
footer {background-color: var(--darkGreen);
  color: var(--brightGreen);
  margin:2%;
  padding:2%;}

/* ----- Anchors ----- Text links */
a {}
a:link {}
a:hover {}
a:active {}
a:visited {}


/*  ----- Navigation ----- Override text links for navigation only.  */
nav {text-align: center;}
nav a {background-color:var(--brightGreen);
  color:var(--darkGreen);
  border: 2px solid var(--forestGreen);
  font-weight:bold;
  text-decoration:none;
  padding:10px;
  margin:auto 5px;
  width:200px;
  align-items: center;}
nav a:hover {background-color:var(--mellowYellow);
  color:var(--darkGreen)}



