    height:100px;
  }

/* ----- Media ----- */
figure {margin:1%; 
  padding:0;
  width:70%;    /* Adjust the percentage based on the combined width of borders and and padding. */
  float:right;
  border:1px solid var(--groundBrown);}
figcaption {font-size:100%;
    text-align:center;
    display:block;
    padding:1%;}
figure img {width:100%; 
    margin:0; 
    padding:0;
    align-items: center;}
